import React, { Component } from 'react';
import  {Grid,Cell } from 'react-mdl';
import Workcard from '../Card/Card';

class work_experience extends Component {
    render() {
        return (
            <div className="work_section">
                <Grid className='work-grid'>
                    <Cell col={4} phone={12} tablet={6} offsetTablet={1}>
                        <Workcard
                            imgSrc={"https://pcrs.ca/wp-content/uploads/2015/11/bc-ed-logo.jpg"}
                            cardTitle={"Data Analyst co-op"}
                            cardText={"Analyzing responses to the Student Learning Survey for Children and Youth in Care. Developed SQL, OBIEE and Tableau skills while making dashboards."}
                            dateRange={"January 2019 – April 2019"}
                        >
                        </Workcard>
                        

                           
                    </Cell>
                    <Cell col={4} phone={12} tablet={6} offsetTablet={1}>
                        <Workcard
                            imgSrc={"https://need2.ca/app/uploads/2015/12/Ministry-of-Children-and-Family.jpg"}
                            cardTitle={"Data Scientist Coop"}
                            cardText={"Converted FoxPro code to Python while working with datasets of Children in Care. Reduced runtime for producing reports."}
                            dateRange={"May 2019 – September 2019"}
                        >
                        </Workcard>
                    </Cell>
                    
                    <Cell col={4} phone={12} tablet={6} offsetTablet={1}>
                        <Workcard
                            imgSrc={"https://cdn.dribbble.com/users/273290/screenshots/1444996/dribbble-logo.jpg"}
                            cardTitle={"Data Analyst Coop"}
                            cardText={"Producing dashboards for daily data requests. Creating and testing dbt models using Jinja and Redshift SQL."}
                            dateRange={"January 2020 – April 2020"}
                        >
                        </Workcard>
                    </Cell>
                    
                </Grid>

            </div>
        )
    }
}

export default work_experience;

