import React from 'react';
import { Card , CardText, CardTitle } from 'react-mdl';

export default function Workcard(props) {
    return (
        <div className="word-card-section">
            <Card shadow={5} style={{ width: '100%', margin: 'auto' }}>
                <CardTitle style={{
                    color: 'black', height: '15em', width: '100%'
                }}>
                    <img src={props.imgSrc}
                        alt="work_place_image" />

                </CardTitle>
                <CardText>
                    <p>{props.cardTitle}</p>
                    <h4>{props.dateRange}</h4>
                    <span className="work-text">{props.cardText}</span>
                </CardText>
            </Card>
        </div>
    );
}