import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import avatar from '../Assets/myAvatar.png';

class landingpage extends Component {
    render() {
        return (
            <div className="landing_page">
                <div style={{width: '100%', margin: 'auto'}}>
                    <Grid className="landing-grid">
                        <Cell col={12}>
                            <img 
                                src={avatar}
                                alt="avatar"
                                className="avatar-img"
                                />
                            <div className="welcome">
                                <h1>Hi there!!!</h1>    
                                <h2>My name is Sujay Damani</h2>
                                <hr/>
                                
                            </div>
                            <div className="social-links">
                                {/* Github*/}
                                <a href="https://github.com/SujayDamani" target="_blank" rel="noopener noreferrer" >
                                    <i className="fa fa-github-square" id="Github"/>
                                </a>
                                {/* Linked-in */}                                
                                <a href="https://www.linkedin.com/in/sujay-damani/" rel="noopener noreferrer" target="_blank" >
                                    <i className="fa fa-linkedin-square" id="linked-in"/>
                                </a>
                            </div>
                            
                            

                        </Cell>
                    </Grid>
                </div>
            </div>
        )
    }
}

export default landingpage;
